/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #fff;
  font-family: "Exo Light", sans-serif;
  font-size: 1rem;
  line-height: 1.5; }

/* Headings */
h1,
