  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background-color: #222222;
  border: 2px solid #00cc00;
  border-radius: 3px;
  color: #00cc00;
  font-size: 1rem;
  font-weight: bold;
  line-height: 1;
  padding: 0.75rem 2.25rem; }
  button:hover, button:active, button:focus,
  input[type="button"]:hover,
  input[type="button"]:active,
  input[type="button"]:focus,
  input[type="reset"]:hover,
  input[type="reset"]:active,
  input[type="reset"]:focus,
  input[type="submit"]:hover,
  input[type="submit"]:active,
  input[type="submit"]:focus {
    border-color: #008c00;
    color: #008c00; }
  @media (min-width: 768px) and (max-width: 1023px) {
    button,
    input[type="button"],
    input[type="reset"],
    input[type="submit"] {
      font-size: 1.25rem; } }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #fff;
  background-color: #222222;
  border: 2px solid #00cc00;
  border-radius: 3px;
  margin-top: 1rem; }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: #fff; }
  @media (min-width: 768px) and (max-width: 1023px) {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="search"],
    textarea {
      font-size: 1.25rem; } }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px; }

textarea {
  padding-left: 3px;
  width: 100%; }

/* New Quote Button */
@media (min-width: 1024px) {
  #new-quote-button {
