/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
    background-color: $color__background-body;
    border: 2px solid $color__green;
    border-radius: 3px;
    color: $color__green;
    font-size: 1rem;
    font-weight: bold;
    line-height: 1;
    padding: ($base__space-unit / 2) ($base__space-unit * 1.5);

    &:hover,
    &:active,
    &:focus {
        border-color: $color__link-hover;
        color: $color__link-hover;
    }

    @include tablet {
        font-size: 1.25rem;
    }
}


/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
    color: #fff;
    background-color: #222222;
    border: 2px solid $color__green;
    border-radius: 3px;
    margin-top: 1rem;

    &:focus {
        color: #fff;
    }

    @include tablet {
        font-size: 1.25rem;
    }
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
    padding: 3px;
}

textarea {
    padding-left: 3px;
    width: 100%;
}

/* New Quote Button */

#new-quote-button {

    @include desktop {
        margin: 0 auto;
    }
}